.MatchCenter {
    margin: 0 auto;
    min-width: 350px;
    display: -ms-inline-flexbox;
    display: inline-flex;
    /* margin-left: 100px; */
}

.Menu{
    height: 50px;
    background-color: #C33325;
}

.MenuButtons{
    position:relative;
    width: 33.3%;
    height: 100%;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    vertical-align: top;
}

.MenuButtons div{
    position:absolute;
    top: 50%;
    color:#F0F1D5;
    height:25px;
    margin-top:-12.5px;
    display: inline-block;
}

.MenuButtons:nth-child(1):hover,
.MenuButtons:nth-child(2):hover,
.MenuButtons:nth-child(3) div:nth-child(2):hover,
.MenuButtons:nth-child(3) div:nth-child(3):hover{
    background: rgb(191, 103, 92);
    cursor: pointer;
}

.MenuButtons:nth-child(3) div{
    width:33.3%;
    vertical-align: top;
    position: relative;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-top: -25px;
    font-size:2rem;
    height: 100%;
}

.Center{
    margin: 0 auto;
    display: block;
}

.PlayersField {
    width: 100%;
    background-color: #2C8693;
}

.PlayersFieldTitle {
    height: 50px;
    background-color: #105187;
    color: #F0F1D5;
    text-align: center;
}

.MatchTitle {
    display: inline-block;
    /* margin-top: 10px; */
    width: 70%;
}

.MatchTitle h2{
    margin-top: 10px;
    font-size:1rem;
}

.PlayersFieldNames {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.PlayersFieldName {
    height: 100px;
    width: 50%;
    border: 1px solid #105187;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Score{
    display: inline-block;
    font-size: 1.3rem;
    width: 15%;
    font-weight: bold;
    font-size: 1.5rem;
    margin-top:10px;
}

.Hide{
    opacity: 0;
}

@media(min-width:600px) {
    .MatchCenter {
        width: 500px;
    }

}


