.Flexbox{
    height: 500px;
    width: 400px;
    -webkit-box-shadow: 1px 1px black;
            box-shadow: 1px 1px black;
    position:fixed;
    z-index: 102;
    top:50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -200px;
    background-color: rgba(255, 255, 255, 0.8);
}

.Hide{
    display: none;
}


