.Toggle {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    background-color: #2C8693;
}

.Toggle div {
    width: 90%;
    height: 3px;
    margin: 4px;
    background-color: #F0F1D5;
}
.Toggle p{
    color:#F0F1D5;
}

.TeamToggle{
    background-color:#C33325;
    display:inline-block;
    padding: 5px;
    border-radius: 5px;
    -webkit-box-shadow: 2px 2px black;
            box-shadow: 2px 2px black;
    color:#F19722;
    text-shadow: 1px 1px black;
}

.TeamToggle:hover{
    cursor:pointer;
    -webkit-box-shadow: none;
            box-shadow: none;
}

@media (min-width: 600px) {
    .Toggle {
        display: none;
    }

    .TeamToggle{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        height: 65px;
        width: 80px;
        vertical-align: middle;
    }
}


