.TeamControl{
    width: 100%;
    height: 100%;
    padding: 2px;
    margin-top: 20px;
    display: inline-block;
}

@media(min-width: 650px){
    .TeamControl{
        -webkit-box-sizing: border-box;
                box-sizing: border-box; 
        margin: 0 auto;
        padding: 2px;
        margin-top: 2px;
        display: block;
        width: 80%;
        vertical-align: top;
    }
}


