.PlayersBlock{
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-flow: column;
        flex-flow: column;
}

.Players h1{
    font-size: 1.1rem;
}

.Players{
    width:100%;

}

@media(min-width: 650px){
    .PlayersBlock{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-line-pack: start;
            align-content: flex-start;
    }

    .Players{
        width:50%;
        margin: 0 auto;
        display: inline-block;
        vertical-align: top;
    }
}


