.NavPanel{
    display: inline-block;
    vertical-align: top;
    margin: 5px 0 5px 0;
    padding-top: 10px;
    list-style: none;
    -ms-flex-align: center;
        align-items: center;
    margin:0;
    padding:0;
    z-index: 100;
    overflow: hidden;
}
.NavPanelColapsed{
    max-height: 50px;
}

.NavPanelOpen{
    -webkit-transition: 2s ease;
    -o-transition: 2s ease;
    transition: 2s ease;
    height: 100%;
}

.Slide{
    position: fixed;
    left:0;
}

.NavIcon{
    width:50px;
    height: 50px;
    background-color:#2C8693;
}
.NavIcon img{
    height:100%;
    width: 100%;
}
.NavPanel ul{
    list-style: none;
    width: 100%;
}

.NavPanel li {
    vertical-align: middle;
    width:100%;
}

@media(min-width: 650px){
    .NavPanel{
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 20%;
        background-color: #2C8693;
        height: 100%;
        vertical-align: top;
        border-radius: 0 0 5px 5px;
    }
}


