/* Individual item */
.Bmitem {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  -webkit-transition: color 0.2s;
  -o-transition: color 0.2s;
  transition: color 0.2s;
}

/* General sidebar styles */
.SidePanel {
  background: #2C8693;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  width: 200px;
  z-index: 110;
  position: fixed;
  left: -355px;
  -webkit-transition:0.5s;
  -o-transition:0.5s;
  transition:0.5s;
}

.SidePanelOpen{
  -webkit-transition:0.5s;
  -o-transition:0.5s;
  transition:0.5s;
    left:0px;
}

@media(min-width:600px){
  .SidePanel{
    display: none;
  }
}


