.SelectTeamButton{
    background-color: #F19722;
    width: 100%;
    padding: 3px;
    min-height: 50px;
    border-radius: 5px;
    margin: 5px;
    color:#2C8693;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}


.SelectTeamButton:hover{
    cursor: pointer;
}

.SelectTeamButton div{
    height: 50px;
}


